.page-header {
  margin-right: 120px;
  margin-left: 120px;
  margin-bottom: 80px;
  margin-top: 38px;
  display: flex;
  justify-content: space-between;
}

.page-header__logo {
  fill: #192252;
  margin-top: 8px;
}

.main-nav {
  align-self: center;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.main-nav__item {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
}
.main-nav__item:not(:last-child) {
  margin-right: 32px;
}

.promo {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.promo__title {
  margin: 0;
  padding: 0;
  margin-top: 135px;
  margin-bottom: 22px;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 72px;
  letter-spacing: -0.01em;
  padding-right: 80px;
}

.promo__description {
  margin: 0;
  padding: 0;
  margin-bottom: 34px;
  padding-right: 120px;
}

.promo__image-wrapper {
  display: flex;
  justify-content: end;
  margin-bottom: 96px;
}

.promo__image {
  display: block;
}
.promo__image:not(:last-child) {
  margin-right: 24px;
}
.promo__image:nth-child(2) {
  padding-top: 80px;
}

.about-us {
  padding-top: 80px;
  padding-bottom: 80px;
  display: grid;
  grid-template-columns: 384px auto;
  gap: 120px;
}

.about-us__image {
  margin-right: 120px;
}

.about-us__title {
  margin: 0;
  padding: 0;
  margin-top: 105px;
  margin-bottom: 24px;
}

.about-us__description {
  margin: 0;
  padding: 0;
  padding-right: 100px;
  padding-bottom: 16px;
}

.about-us__button {
  margin-top: 16px;
}

.benefits {
  margin-top: 80px;
  margin-bottom: 80px;
}

.benefits__card {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;
}

.benefits__description {
  width: 580px;
}

.benefits__image--left {
  order: -1;
}

.reservation {
  margin-top: 80px;
  margin-bottom: 80px;
}

.reservation__wrapper {
  margin-left: 300px;
  margin-right: 300px;
}

.reservation__title {
  margin: 0 auto;
  margin-bottom: 24px;
  padding: 0;
  text-align: center;
}

.reservation__description {
  margin: 0;
  padding: 0;
  text-align: center;
  margin-bottom: 32px;
}

.reservation__button {
  margin: 0 auto;
  margin-bottom: 88px;
}

.reservation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}

.reservation__item {
  width: 384px;
}
.reservation__item:not(:last-child) {
  margin-right: 24px;
}

.reservation__item-title {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  color: #000000;
}

.reservation__item-description {
  margin: 0;
  padding: 0;
  margin-bottom: 32px;
}

.reservation__item-link {
  color: #424f7b;
  text-decoration: none;
  cursor: pointer;
}

.room {
  display: flex;
  justify-content: space-between;
  margin-top: 80px;
  margin-bottom: 80px;
}

.room__wrapper {
  margin-top: 80px;
}

.room__subtitle {
  font-size: 16px;
  line-height: 24px;
  margin: 0;
  padding: 0;
  margin-bottom: 12px;
}

.room__title {
  margin: 0;
  padding: 0;
  margin-bottom: 24px;
}

.room__description {
  margin: 0;
  padding: 0;
  margin-bottom: 32px;
  padding-right: 280px;
}

.room__links-wrapper {
  display: flex;
}

.room__button {
  margin-right: 24px;
}

.room__link {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #0e1734;
  text-decoration: none;
  display: block;
  align-self: center;
}

.feedbacks {
  padding-top: 80px;
  padding-bottom: 80px;
}

.feedbacks__wrapper {
  margin-right: 200px;
  margin-left: 200px;
}

.feedbacks__title {
  margin: 0;
  padding: 0;
  margin-bottom: 62px;
  text-align: center;
}

.feedbacks__avatar {
  margin: 0 auto;
  margin-bottom: 44px;
  display: block;
}

.feedbacks__text {
  margin: 0;
  padding: 0;
  margin-bottom: 40px;
  text-align: center;
}

.feedbacks__author {
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: #192252;
  margin-bottom: 24px;
}

.feedbacks__button-list {
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: center;
  list-style-type: none;
}

.page-footer {
  background-color: #08265a;
  padding: 120px;
  color: #ffffff;
  display: grid;
  gap: 400px;
  grid-template-columns: 200px auto;
}

.page-footer__logo {
  fill: #ffffff;
  margin-bottom: 12px;
}

.page-footer__text {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 28px;
  color: #d9dbde;
  margin-bottom: 36px;
}

.page-footer__social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.page-footer__social-item:not(:last-child) {
  margin-right: 32px;
}

.page-footer_social-logo {
  fill: #ffffff;
}

